* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.7;
}

.nav-list {
  height: 10vh;
  display: flex;
  width: 50%;
  margin-left: auto;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}

.nav-links {
  text-decoration: none;
  color: #000;
  font-size: 1.8rem;
}

.home {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home-content {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 100%;
}

.home {
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}
